class list {
	constructor(capacity) {
		this.element = [];
		this.nowIndex = -1;
		this.cap = capacity;
	}

	// 获取指针位置
	getNowIndex() {
		return this.nowIndex;
	}

	// 获取当前元素并把指针向前移
	getNowAndForward() {
		if (this.nowIndex < 0) return;
		this.nowIndex--;
		return this.element[this.nowIndex]
	}

	// 获取当前元素并把指针向后移
	getNowAndBackward() {
		if (this.nowIndex < 0) return;
		this.nowIndex++;
		return this.element[this.nowIndex]
	}

	// 获取i对应的元素并把指针指向它
	getAndSetNow(i) {
		if (i < 0 || i > this.size()) return;
		this.nowIndex = i;
		return this.element[this.nowIndex]
	}

	// 除了链头的origin全都清除，改变指针指向
	reset() {
		// console.log("reset0 ", this.element)
		this.element.splice(1);
		this.nowIndex = 0;
		// console.log("reset1 ", this.element)
		return;
	}

	// 把指针后面的全清除
	removeBackPart() {
		if (this.nowIndex == -1) return;
		this.element.splice(this.nowIndex + 1)
		return
	}

	at(i) {
		if (i < 0 || i > this.element.length) {
			console.log("Invalid index")
			return
		}
		return this.element[i]
	}

	push(e) {
		if (e == undefined) {
			console.log('push undefined.');
			return;
		}
		if (this.element.length == this.cap) {
			this.element.shift();
			console.log('超过容量，挤掉栈底')
		}
		this.element.push(e);
		this.nowIndex++
		return;
	}

	pop() {
		if (this.element.length == 0) return;
		this.nowIndex--
		return this.element.pop();
	}
	
	back() {
		return this.element[this.element.length - 1];
	}

	size() {
		return this.element.length;
	}

	empty() {
		return this.element.length == 0;
	}

	clear() {
		this.element.splice(0, this.element.length);
		this.top = 0;
		return;
	}
}

export {
	list
}