<template>
  <v-container fluid>
	<!-- 栅格Reference：https://vuetifyjs.com/zh-Hans/components/grids/ -->
	<v-row>
		<v-col cols="12">
			<v-row align="center" justify="center" class="grey lighten-5">
				<img v-show="false" id="myImg1" src="../assets/default1.png"/>
				<img v-show="false" id="myImg2" src="../assets/default2.png"/>
				<img v-show="false" id="myImg3" src="../assets/default3.png"/>
				<canvas id="myCanvas"></canvas>
			</v-row>
			<v-row align= "stretch" justify="center">
				<v-col cols="5">
					<v-select label="选择图片"
						v-model="choice" v-if="hackReset"
						:items="defaultImgs" 
						item-text="name"
						item-value="value"
						outlined
						@change="showWarningMsg" 
					></v-select>
				</v-col>
				<input id="uploadImg" type="file" style="display: none" v-on:change="getUserImg"/>
				<v-btn v-on:click="download" class="ma-4">
					保存图片
				</v-btn>
			</v-row>
		</v-col>
	</v-row>
  </v-container>
</template>

<script>
  export default {
    name: 'midBlock',
	data() {
		return {
			choice: 1,
			lastChoice: 1,
			confirmChange: true,
			defaultImgs: [
				{name: 'flower', value: 1},
				{name: 'nature', value: 2},
				{name: 'person', value: 3},
				{name: 'custom', value: 0},
			],
			firstCallHandler: true, // 组织循环触发
			hackReset: true,		// 强制select组件重新渲染
		}
	},
	watch: {
		choice: {
			handler(curVal, oldVal) {
				/* 
					监听v-select组件的变化
						若确认修改图片，调用修改函数
						否则，选框重新渲染，恢复旧值
				*/
				console.log('监听到改变: ',curVal, oldVal, 
							'用户是否确认改变: ',this.confirmChange);
				this.lastChoice = oldVal;
				if (this.confirmChange)	{	// 确认修改，调用修改函数
					this.changeImg(curVal);
				}
				else if (this.firstCallHandler) {
					this.firstCallHandler = false;
					this.choice = this.lastChoice;	// 恢复旧值
					this.hackReset = false;		// 选框重新渲染
					this.$nextTick(() => {
						this.hackReset = true
					});
				} else {
					this.firstCallHandler = true;	// 重置
				}
			}
		}
	},
	mounted: async function(){
		console.log('进入图片预设函数');
		var canvas = document.getElementById("myCanvas");
		if(canvas.getContext) {
			console.log('浏览器支持Canvas!');
			await this.fillCanvas(1, this.callback);  // 图片绘制到画布上
			console.log('准备将画布传给父组件');
			this.$emit('canvasReady', canvas); // 将画布传给父组件
		}
		else {
			alert('抱歉，您的浏览器不支持Canvas，请更新版本后重试');
		}
	},
	methods: {
		// ============================== 顶层功能函数  ============================== 
		download() {
			// 图片下载（待完善）
			console.log('图片下载到本地');
			var aTag = document.createElement('a');
			var canvas = document.getElementById('myCanvas');
			canvas.toBlob(function(blob) {
				aTag.download = 'imgFile.jpg';
				aTag.href = URL.createObjectURL(blob);
				aTag.click();
			}, "image/jpeg", 1.0);
		},
		getUserImg: async function () {
			/* 
				读取用户上传的图片并转码
				将新图片更新到画布上，并传回给父组件
			*/
			var fileInput = document.getElementById("uploadImg");
			if (fileInput.files.length == 0) {
				console.log('没有可用文件');
				return;
			}
			// 等待转码结果返回
			var rawImgData = undefined;
			await this.getRawImgData(fileInput.files[0]).then(function (res) {
				rawImgData = res;
			});
			// 将新图片写入画布
			this.changeImg(rawImgData);
		},
		// ================================ 1级 辅助函数  ==============================
		showWarningMsg () {
			// 更改图片前发出确认提示
			this.confirmChange = confirm("更改图片后，当前画布上的图片将会丢失，是否确认？");
		},
		getRawImgData (file) {
			// 功能：读取用户上传的图片并转码
			return new Promise((resolve) => {
				var reader = new FileReader();
				// 以DataURL的形式读取文件, 发起一个异步操作来读取文件内容
				reader.readAsDataURL(file);
				reader.onload = function(e) {  // 文件读取完后调用
					resolve(e.target.result);	// 返回转码结果
				}
			});
		},
		changeImg: async function (newImgSrc) {
			/*
				功能：将新图片更新到画布上，并传回给父组件
				newImgSrc:
					case1: 当用户选择上传自定义图片时，取值为 0；
					case2：当用户选择默认图时，取值为默认图编号；[1,2,3]
					case3：当用户的自定义图片转码成功后，取值为图片编码；[data:image/jpeg/...base64]
			*/
			this.$store.state.opList.clear();	// 清空历史记录
			
			if (newImgSrc == 0) { // case1，点击触发input元素
				document.getElementById("uploadImg").click();
			}
			await this.fillCanvas(newImgSrc, this.callback);  // 图片绘制到画布上
			console.log('准备将画布传给父组件');
			this.$emit('canvasReady', document.getElementById("myCanvas")); // 将画布传给父组件
		},
		// ================================ 次级 辅助函数  ============================== 
		fillCanvas (imgSrc, callback) {
			// 辅助changeImg函数：将新图片更新到画布上
			return new Promise((resolve) => {
				var img = undefined;
				if (imgSrc == 1 || imgSrc == 2 || imgSrc == 3) { // 处理case2
					img = document.getElementById("myImg" + imgSrc);
				}
				else { // 处理case3
					img = new Image();
					img.crossOrigin = '';
					img.src = imgSrc;
				}
				
				// 兼容性图片onload事件
				img.onload = function(){
					console.log('调用onload');
					callback(img);
					resolve();
				}
				if (img.complete) {
					console.log('调用complete');
					callback(img);
					resolve();	// 跳过onload直接返回
				}
			});
		},
		callback (img) {
			/*
				fillCanvas的回调函数
				- 画布清空
				- 如果新图片超过屏幕大小，缩小图片
				- 将画布大小与新图片大小设为一致
				- 绘制图片
			*/
			var canvas = document.getElementById("myCanvas");
			var ctx = canvas.getContext('2d');
			var scale = 0.75;
			
			console.log('清空画布');
			ctx.clearRect(0, 0, canvas.offsetWidth, canvas.offsetHeight);
			
			// 判断是否需要缩小图片，计算宽高比不变的前提下的缩小比率
			canvas.width = Math.min(document.body.clientWidth * scale, img.width);
			canvas.height = Math.min(document.body.clientHeight * scale, img.height);
			var xRate = canvas.width / img.width;
			var yRate = canvas.height / img.height;
			var setRate = xRate < yRate ? xRate : yRate;
			setRate = Math.min(1, setRate); // 允许缩小，不允许放大
			
			// 计算调整后的图片大小
			var dW = img.width * setRate; 
			var dH = img.height * setRate;
			
			// 设置画布大小和图片一致
			canvas.width = dW; 
			canvas.height = dH;
			
			// 绘制
			ctx.drawImage(img, 0, 0, dW, dH);
			console.log('画布绘制完成');
		},
	},
  }
</script>
