<template>
  <v-container fluid>
    <v-row>
      <v-col cols="12">
        <v-row align="center" justify="center">
          <v-btn-toggle dense borderless background-color="white accent-3">
            <v-btn @click="undo">
              <!-- 撤销 -->
              <v-icon dark>mdi-undo</v-icon>
            </v-btn>

            <v-btn @click="redo">
              <!-- 重做 -->
              <v-icon dark>mdi-redo</v-icon>
            </v-btn>

            <v-btn @click="resetToOrigin">
              <!-- 恢复原图 -->
              <v-icon dark>mdi-restore</v-icon>
            </v-btn>
          </v-btn-toggle>
        </v-row>
      </v-col>
    </v-row>

    <!-- 历史记录 -->
    <v-subheader>历史记录</v-subheader>
    <v-list shaped>
      <v-list-item-group v-model="curState" color="primary">
        <v-list-item
          v-for="(item, i) in this.$store.state.opList.element"
          :key="i"
          @click="change(i)"
        >
          <v-list-item-content>
            <v-list-item-title v-text="item.name"></v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list-item-group>
    </v-list>
  </v-container>
</template>

<script>
import { edit } from "../js/operator";

export default {
  name: "history",
  props: ["canvas", "origin"], // 画布，原图数据
  data() {
	return {
		curState: 0,
	}
  },
  watch: {
	listenList: {
		// 监听列表变化，更新高亮条目
		handler(curVal) {
			this.curState = curVal;
		}
	}
  },
  computed: {
	listenList () {
		return this.$store.state.opList.getNowIndex();
	}
  },
  // computed: {
  //   reverseData() {
  //     var ret = {};
  //     ret = JSON.parse(JSON.stringify(this.$store.state.opList.element));
  //     return ret.reverse();
  //   }
  // },

  methods: {
    resetToOrigin() {
      if (typeof this.origin == "undefined") return;
      if (confirm("您点击了恢复原图，该操作不可撤销，是否确认？")) {
        edit(this.canvas, this.origin);
        // this.$store.state.opList.clear();
        this.$store.state.opList.reset();
        console.log("opList:", this.$store.state.opList.size());
        // console.log("redoList:", this.$store.state.redoList.size());
      }
    },
    undo() {
      if (this.$store.state.opList.getNowIndex() == 0) {
        alert("没有可以撤销的操作了");
        return;
      }
      console.log("undo canvas ", this.canvas);
      const now = this.$store.state.opList.getNowAndForward();
      const imgData = now.data;
      // const funcName = now.name;
      edit(this.canvas, imgData);
      // this.$store.state.redoList.push({
      //   data: edit(this.canvas, imgData),
      //   name: funcName
      // });
      console.log("opList:", this.$store.state.opList.size());
    },
    redo() {
      if (
        this.$store.state.opList.getNowIndex() ==
        this.$store.state.opList.size()
      ) {
        alert("没有可以重做的操作了");
        return;
      }
      const now = this.$store.state.opList.getNowAndBackward();
      const imgData = now.data;
      // const funcName = top.name;
      edit(this.canvas, imgData);
      // this.$store.state.opList.push({
      //   data: edit(this.canvas, imgData),
      //   name: funcName
      // });
      console.log("opList:", this.$store.state.opList.size());
      // console.log("redoList:", this.$store.state.redoList.size());
    },
    change(i) {
      const now = this.$store.state.opList.getAndSetNow(i);
      if (now == undefined) {
        console.log("非法操作");
        return;
      }
      console.log("history i: ", i);
      const imgData = now.data;
      edit(this.canvas, imgData);
    }
  }
};
</script>