<template>
  <v-container fluid>
    <!-- 功能抽屉 -->
    <v-list>
      <v-list-group v-for="item in items" :key="item.title" :prepend-icon="item.icon" no-action>
        <template v-slot:activator>
          <v-list-item-content>
            <v-list-item-title v-text="item.title"></v-list-item-title>
          </v-list-item-content>
        </template>

        <v-list-item
          v-for="(subItem, idx) in item.items"
          :key="subItem.title"
          @click="item.action(idx)"
        >
          <v-list-item-content>
            <v-list-item-title v-text="subItem.title"></v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list-group>
    </v-list>
  </v-container>
</template>

<script>
import { parsing } from "../js/operator";

export default {
  name: "basicOperate",
  props: ["canvas", "origin"], // 画布，原图数据
  data() {
    return {
      items: [
        {
          icon: "mdi-pencil",
          title: "基础编辑",
          action: this.basicOP,
          items: [
            { title: "色彩反转" },
            { title: "去色" },
            { title: "浮雕" },
            { title: "水平翻转" },
            { title: "垂直翻转" },
            { title: "黑白" },
            { title: "高斯模糊" }
          ]
        },
        {
          icon: "mdi-pencil",
          title: "风格编辑",
          items: [
            // { title: '添加滤镜'},
          ]
        }
      ]
    };
  },
  methods: {
    basicOP(n) {
      if (typeof this.$store.state.opList == "undefined") {
        console.log("op undefined");
        return;
      }
      this.$store.state.opList.removeBackPart();
      this.$store.state.opList.push(parsing(this.canvas, n));
      //   console.log("opList:", this.$store.state.opList.size());
      console.log("opList: ", this.$store.state.opList);
    }
  }
};
</script>

