<template>
  <v-app id="inspire">
    <v-app-bar app clipped-right clipped-left :color="barColor" dark>
      <v-app-bar-nav-icon @click.stop="drawer = !drawer" />
      <v-toolbar-title>OnlinePS</v-toolbar-title>
      <v-spacer />
      <v-app-bar-nav-icon @click.stop="drawerRight = !drawerRight" />
    </v-app-bar>

    <!-- 右边侧边栏 -->
    <v-navigation-drawer v-model="drawerRight" app width="auto" right clipped>
      <v-list dense>
        <v-list-item>
          <history :canvas="canvas" :origin="originImgData"></history>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <!-- 左侧侧边栏 -->
    <v-navigation-drawer v-model="drawer" app width="auto" clipped>
      <v-list dense>
        <v-list-item>
          <basicOperate :canvas="canvas" :origin="originImgData"></basicOperate>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-navigation-drawer v-model="left" fixed temporary />

    <v-content>
      <v-container class="fill-height" fluid>
        <v-row justify="center" align="center">
          <midBlock @canvasReady="canvasReady"></midBlock>
        </v-row>
      </v-container>
    </v-content>

    <v-navigation-drawer v-model="right" fixed right temporary />

    <v-footer app :color="barColor" class="white--text">
      <span>SETeam06</span>
      <v-spacer />
      <span>&copy; 2020</span>
    </v-footer>
  </v-app>
</template>

<script>
import midBlock from "./components/midBlock";
import basicOperate from "./components/basicOperate";
import history from "./components/history";
// import list from "./js/list";

export default {
  props: {
    source: String
  },
  data: () => ({
    drawer: null,
    drawerRight: null,
    left: false,
    right: false,
    barColor: "black",
    canvas: undefined,
    originImgData: undefined
  }),
  methods: {
    canvasReady: function(e) {
      this.canvas = e;
      console.log("接收到画布", this.canvas);
      var cxt = this.canvas.getContext("2d");
      var imageData = cxt.getImageData(
        0,
        0,
        this.canvas.width,
        this.canvas.height
      );
      this.originImgData = new Uint8ClampedArray(imageData.data);
      this.$store.state.opList.push({ data: this.originImgData, name: "原图" });
      console.log("原图数据解析完成");
    }
  },
  components: {
    midBlock,
    basicOperate,
    history
  }
};
</script>