import Vue from 'vue'
import Vuex from 'vuex'
import { mapGetters } from 'vuex'
import { list } from '../js/list'
Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        opList: new list(10),
    },
    computed: {
        // mapState({

        // })
        ...mapGetters({
            opList: 'opList'
        })
    }
})

export default store